* {
  margin: 0;
}

@font-face {
    font-family: 'header';
    src: url(/memory-card/static/media/Caveat-VariableFont_wght.7519ccd6.ttf);
}

.header {
    background-color: rgb(63, 63, 63);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    padding: 16px;
}

.header-title {
    font-family: 'header', sans-serif;
    font-size: 80px;
    font-weight: bold;
}

.header-description {
    font-size: 24px;
}
body {
    background-color: #eee;
}

.scoreboard {
    margin: 16px;
    font-weight: bold;
    font-size: 20px;
}

.restart {
    border: none;
    border-radius: 8px;
    background-color: rgb(132, 51, 139);
    color: white;
    font-weight: bold;
    font-size: 20px;
    margin: 16px;
    padding: 8px 16px;
    cursor: pointer;
}

.restart:hover {
    background-color: rgb(98, 37, 104);
}

.card {
    height: 200px;
    width: auto;
    cursor: pointer;
}

.card:hover {
    -webkit-transform: scale(1.05,1.05);
            transform: scale(1.05,1.05);
}

.card-display {
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    flex-shrink: 0;
    flex-wrap: wrap;
    margin: 10px;
}
